$('.portfolio__slider').slick({
    fade: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: true,
    appendDots: $('.portfolio__dots'),
    customPaging : function(slider, i) {
        var thumb = $(slider.$slides[i]).data();

        return '0' + (i + 1);
    },
    dotsClass: 'portfolio__dots-list',
    prevArrow: $('.portfolio__prev'),
    nextArrow: $('.portfolio__next'),
});

$('.partners__slider').slick({
    infinite: true,
    slidesToShow: 7,
    slidesToScroll: 1,
    autoWidth: true,
    dots: true,
    appendDots: $('.partners__dots'),
    customPaging : function(slider, i) {
        var thumb = $(slider.$slides[i]).data();

        return '0' + (i + 1);
    },
    dotsClass: 'partners__dots-list',
    prevArrow: $('.partners__prev'),
    nextArrow: $('.partners__next'),
    responsive: [
        {
            breakpoint: 1801,
            settings: {
                slidesToShow: 6,
            }
        },
        {
            breakpoint: 1601,
            settings: {
                slidesToShow: 5,
            }
        },
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 4,
            }
        },
        {
            breakpoint: 769,
            settings: {
                slidesToShow: 3,
            }
        },
        {
            breakpoint: 577,
            settings: {
                slidesToShow: 2,
            }
        },
        {
            breakpoint: 500,
            settings: {
                slidesToShow: 1,
            }
        },
    ]
});

